import React, { useEffect } from "react";
import ReactGA from 'react-ga';
import "./styles.scss";
import LazyLoad from 'react-lazyload';

function isMobile() {
    return /Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent);
}

export default function AmazonAd({
    type
}) {
    useEffect(() => {
        if (type) {
            ReactGA.event({
                category: 'AmazonAd',
                action: 'Appeared',
                value: isMobile(),
                label: type,
            });
        }
    });

    let src = "";
    let width = "";
    let height = "";
    if (type === "cameras") {
        if (isMobile()) {
            src = "//rcm-na.amazon-adsystem.com/e/cm?o=1&p=12&l=ur1&category=camera&banner=1J9T4PKYP3SC43AWZ002&f=ifr&linkID=87f86e1f0c3cc806bfbde25f48d779be&t=onew1ng3dang3-20&tracking_id=onew1ng3dang3-20";
            width = "300";
            height = "250";
        } else {
            src = "//rcm-na.amazon-adsystem.com/e/cm?o=1&p=48&l=ur1&category=camera&banner=1GWHT8CGRRD8T85HE682&f=ifr&linkID=58672512e8744f3d19c0cb01a25cc260&t=onew1ng3dang3-20&tracking_id=onew1ng3dang3-20";
            width = "728";
            height = "90";
        }
    } else if (type === "computers") {
        if (isMobile()) {
            src = "//rcm-na.amazon-adsystem.com/e/cm?o=1&p=12&l=ur1&category=computers_accesories&banner=19KA014JRX72FW0QJ7R2&f=ifr&linkID=01ca46394e69a20776cd098502cf3d73&t=onew1ng3dang3-20&tracking_id=onew1ng3dang3-20";
            width = "300";
            height = "250";
        } else {
            src = "//rcm-na.amazon-adsystem.com/e/cm?o=1&p=48&l=ur1&category=computers_accesories&banner=0WPSTQ1FKE5PHW0XQSG2&f=ifr&linkID=2c3884f5860a861eaa4e48c2dfc5c0e8&t=onew1ng3dang3-20&tracking_id=onew1ng3dang3-20";
            width = "728";
            height = "90";
        }
    }

    if (src !== "") {
        return (
            <LazyLoad height={parseInt(height)} offset={parseInt(height)}>
                <div className="AmazonAdContainer">
                    <iframe src={src} width={width} height={height} scrolling="no" border="0" marginwidth="0" style={{ border: "none" }} frameborder="0"></iframe>
                </div>
            </LazyLoad>
        );
    }
    return null;
}
