import React, {useEffect, useState} from "react";
import { StaticQuery, graphql } from 'gatsby';
import Layout from "../../../components/layout";
import ReactGA from 'react-ga';
// Components
import SEO from "../../../components/SEO";
import Link from "../../../components/Link";
import Image from "../../../components/Image";

import {createProductMetadata} from "../../../jsonLd";

import "./styles.scss";

export default function Template() {
  const [start] = useState(new Date());
  useEffect(() => {
    return () => {
      const cur = new Date();
      const diff = cur.getMilliseconds() - start.getMilliseconds();
      ReactGA.timing({
        category: 'TimeOnMerch',
        variable: 'time',
        value: diff, // in milliseconds
        label: "Merch",
      });
    };
  });

  return (
    <Layout>
      <StaticQuery
        query={graphql`
              query Merch {
                merchDataJson {
                    title
                    subtitle
                    content {
                      id
                      url
                      type
                      photo
                      priceText
                      currency
                      price
                      title
                    }
                }
            }
          ` }
        render={(json) => {
          const {merchDataJson: data} = json;

          const productsMetadata = data.content.map(d => {
            return createProductMetadata({
              name: d.title,
              brand: d.type,
              description: d.title,
              image: d.photo,
              price: d.price,
              priceCurrency: d.currency,
              url: d.url,
            });
          });

          const graph = {
            "@context": "http://schema.org",
            "@graph": productsMetadata,
          }

          return (<>
            <SEO
              title="TechWaifu's Merchandise Store"
              metadata={JSON.stringify(graph)}
            />
            <div className="MerchStore">
              <h1>{data.title}</h1>
              <div className="Subtitle">{data.subtitle}</div>

              <div className="MerchLinkContainer">
                {data.content.map((data) => {
                  return (
                    <div className="MerchLink" key={data.id}>
                      <Link href={data.url} title={data.title}>
                        <Image src={data.photo} alt={data.title} />
                        <div className="ProductName">
                          {data.title}
                        </div>
                        <div className="Price">
                          {data.priceText}
                        </div>
                      </Link>
                    </div>
                  );
                })}
              </div>
            </div>
          </>);
        }} />
    </Layout >
  )
}
